import React, { useEffect, useMemo, useState } from "react";
import Item from "./item.js";
import "./style.css";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

//Filter list by category in React JS
function Board() {
  // Default Value
  var defaultSports = [
    { name: "Table Tennis", category: "Indoor" },
    { name: "Football", category: "Outdoor" },
    { name: "Swimming", category: "Aquatics" },
    { name: "Chess", category: "Indoor" },
    { name: "BaseBall", category: "Outdoor" }
  ];
  const [sportList, setSportList] = useState([]);

  const [selectedCategory, setSelectedCategory] = useState();

  // Add default value on page load
  useEffect(() => {
    setSportList(rows);
  }, []);

  // Function to get filtered list
  function getFilteredList() {
    // Avoid filter when selectedCategory is null
    if (!selectedCategory) {
      return sportList;
    }
    return sportList.filter((item) => item.cat === selectedCategory);
  }

  // Avoid duplicate function calls with useMemo
  var filteredList = useMemo(getFilteredList, [selectedCategory, sportList]);

  function handleCategoryChange(event) {
    setSelectedCategory(event.target.value);
  }

  const rows = [
    createData('獎學金', '2022/03/31', '七星獎助學金申請', 0, 'https://www.bse.ntu.edu.tw/news?detail=108'),
    createData('系所公告', '2022/03/31', '111學年度博士班口試時間表', 1), 'https://www.bse.ntu.edu.tw/news?detail=108',
    createData('獎學金', '2022/03/31', '思鴻教育基金會提供生農學院畢業學生兩年期全額獎學金', 0, 'https://www.bse.ntu.edu.tw/news?detail=108'),
    createData('獎學金', '2022/03/31', '思鴻教育基金會提供生農學院畢業學生兩年期全額獎學金', 0), 'https://www.bse.ntu.edu.tw/news?detail=108',
    createData('活動訊息', '2022/03/31', '111年度經濟部工業局DIGI+Talent及TCA計畫大學校院申請說明會' ,2, 'https://www.bse.ntu.edu.tw/news?detail=108'),
  ];

  function createData(cat, date, title, cid, url) {
    return { cat, date, title, cid, url };
  }

  const colors = ['green', 'blue', 'grey', 'yellow']

  function openInNewTab(url) {
    window.open(url, '_blank').focus();
   }
  
  return (
    <div style={{height:"300px", width:"80vw", margin:"10%"}}>
        <h3>最新消息</h3>
      <div className="filter-container">
        <div>消息分類:</div>
        <div>
          <select
            name="category-list"
            id="category-list"
            onChange={handleCategoryChange}
          >
            <option value="">All</option>
            <option value="獎學金">獎學金</option>
            <option value="系所公告">系所公告</option>
            <option value="活動訊息">活動訊息</option>
          </select>

        </div>
      </div>

      {/* <div className="sport-list">
        {filteredList.map((element, index) => (
          <Item {...element} key={index} />
        ))}
      </div> */}

      <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>消息分類</TableCell>
            <TableCell align="right">上架日期</TableCell>
            <TableCell align="right">標題</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {filteredList.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row"><div style={{ background: colors[row.cid] , color: "white", borderRadius:"5px", display:"flex", justifyContent:"center"}}>{row.cat}</div></TableCell>
              <TableCell align="right">{row.date}</TableCell>
              <TableCell align="right" onClick={()=>openInNewTab(row.url)} style={{cursor:"pointer"}}>{row.title}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </div>
  );
}
export default Board;